﻿/*红色*/
.bg-red {
    background-color: #FF5722;
    color:#fff;
}
/*万能灰色*/
.bg-gray-u {
    background-color: #efefef;
    color: #333;
}
/*浅蓝色*/
.bg-blue {
    background-color: #3598dc;
    color: #fff;
}
/*青色*/
.bg-cyan {
    background-color: #2F4056;
    color: #fff;
}
/*橙色*/
.bg-orange {
    background-color: #c49f47;
    color: #fff;
}

/*藏蓝色*/
.bg-navy {
    background-color: #2d5f8b;
    color: #fff;
    box-shadow: 0 2px 2px 0 rgba(104,104,104,0.14);

    &:hover{
        color: #fff;
        background-color: #4276a4;
        box-shadow: 0 2px 2px 0 rgba(104,104,104,0.14);
    }
}

.bg-navy-light {
    background-color: #4276a4;
    color: #fff; 
    &:hover{
        color: #fff;
        background-color: #2d5f8b;
        box-shadow: 0 2px 2px 0 rgba(104,104,104,0.14);
    }
}
